import React, {useState} from 'react';
import {
	Box,
	Container,
	IconButton,
	InputAdornment,
	TextField,
	Typography,
	useMediaQuery,
	useTheme
} from "@mui/material";
import BackButton from "../components/BackButton";
import PageWrapper from "../components/PageWrapper";
import {fetchService} from "../services/fetchService";
import {useLoaderData} from "react-router-dom";
import TableContent from "../components/TableContent";
import ClearIcon from "@mui/icons-material/Clear";
import MapIcon from '@mui/icons-material/Map';
import MapModal from "../components/MapModal";

export async function loader({params}) {
	const company = await fetchService(`/clients/${params.id}`, 'GET');
	const companyProducts = await fetchService(`/clients/${params.id}/products`, 'GET');
	return {company, companyProducts};
}

const CompanyDetailPage = () => {
	const [modalIsOpen, setModalIsOpen] = useState(false);
	const {company, companyProducts} = useLoaderData();
	const {
		id,
		title,
		phone,
		email,
		contact_person,
		description,
		address,
		location,
		is_active
	} = company;
	const theme = useTheme();
	const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));

	const [filter, setFilter] = useState('');

	const headers = ['',
		" ",
		"",
		" ",
		" "];

	return (
		<PageWrapper title={title}>
			<Container
				sx={{
					display: 'flex',
					flexWrap: 'wrap',
					justifyContent: 'center',
					gap: {'xs': '1rem', 'md': '3rem'},
					fontSize: {'xs': '.8rem', 'md': '1.3rem'}
				}}
			>
				<Typography
					variant={isSmallScreen ? 'h6' : 'h5'}
				>{email} </Typography>
				<Typography
					variant={isSmallScreen ? 'h6' : 'h5'}
				>{phone}</Typography>
				<Typography
					variant={isSmallScreen ? 'h6' : 'h5'}
				>{contact_person}</Typography>
				<IconButton
					aria-label=' '
					onClick={() => setModalIsOpen(true)}
					edge={'end'}
				>
					<MapIcon/>
					<Typography> </Typography>
				</IconButton>
			</Container>
			<Typography
				variant={isSmallScreen ? 'h4' : 'h2'}
				sx={{fontWeight: 'bold'}}
				gutterBottom
			>
				{title}
			</Typography>
			<Typography
				paragraph
				gutterBottom
			>
				{description}
			</Typography>
			<Box
				sx={{
					display: 'flex',
					flexDirection: 'column',
				}}
			>
				<TextField
					id='search'
					label=''
					variant='standard'
					sx={{width: '15rem', margin: '0 auto 1rem'}}
					value={filter}
					onChange={e => setFilter(e.target.value)}
					InputProps={filter.length > 0 ? {
						endAdornment: <InputAdornment position='end'>
							<IconButton
								aria-label='clear'
								onClick={() => setFilter('')}
								edge='end'
							>
								<ClearIcon/>
							</IconButton>
						</InputAdornment>,
					} : undefined}
				/>
				<TableContent
					content={companyProducts.filter(p => p.title.toLowerCase().includes(filter.toLowerCase()))}
					headers={headers}
				/>
				<Box sx={{marginTop: 'auto'}}>
					<BackButton/>
				</Box>
			</Box>
			<MapModal
				markerTitle={title}
				isOpen={modalIsOpen}
				onClose={() => setModalIsOpen(false)}
				coords={location ? JSON.parse(location) : undefined}
			/>
		</PageWrapper>
	);
};

export default CompanyDetailPage;
